import MD5 from 'js-md5'

export const getMd5 = MD5;

/*
* ()
* @param {string} str 
* @param {String} s   
* */
export function trim(str, s) {
  if (typeof str == 'string') {
    let l = ltrim(str, s)
    let r = rtrim(l, s)
    return r;
  }
  else {
    return str
  }
}

/*
* ()
* @param {string} str 
* @param {String} s   
* */
export function ltrim(str, s) {
  if (typeof str == 'string') {
    if (!s) s = '\\s'
    else s = s.replace(/([\(\)\[\]\*\+\\])/g, '\\$1');
    var reg = new RegExp('^(' + s + ')*')
    return str.replace(reg, '')
  }
  else {
    return str
  }
}

/*
* ()
* @param {string} str 
* @param {String} s   
* */
export function rtrim(str, s) {
  if (typeof str == 'string') {
    if (!s) s = '\\s'
    else s = s.replace(/([\(\)\[\]\*\+\\])/g, '\\$1');
    var reg = new RegExp('(' + s + ')*$')
    return str.replace(reg, '')
  }
  else {
    return str
  }
}

/*
* 
* @param {String} string 
* */
export function copyText(string) {
  var oInput = document.getElementById('ici-copy');

  if (!oInput) {
    var oInput = document.createElement('input');
    oInput.setAttribute('id', 'ici-copy');
    oInput.setAttribute('type', 'text');
    oInput.style.position = 'fixed';
    oInput.style.bottom = '-40px';
    oInput.style.left = '-40px';
    document.body.appendChild(oInput);
  }

  oInput.value = string;
  oInput.select(); // 
  document.execCommand("Copy"); // 
  //console.log('');
}

/*
* \r\nhtml<br>
* @param {String} string 
* */
export function rnTobr(string) {
  if (typeof string === "string" && string.indexOf('\n') > -1) {
    return string.replace(/\n/g, '<br>')
  }
  else {
    return string;
  }
}


//
//@param str
//@param key
// @param color ;

export function boldKey(str, key, color) {
  if (!color) color = 'rgb(77,200,107)';
  if (typeof str === 'string' && typeof key === 'string') {
    var s = key.replace(/([\(\)\[\]\*\\])/g, '\\$1').replace(/\+/g, '');
    return str.replace(new RegExp('(' + s + ')', 'ig'), `<b style="color:${color}">$1</b>`)
  }
  else {
    return str;
  }
}

/**
 *
 * @param {String}str 
 * @param {Boolean}isWord 
 */
export function capitalize(str,isWord) {

  if (typeof (str) == 'string') {

    str = str.toLowerCase()

    if(isWord){
      var arr = str.split(/ +/)
      return arr.map((n) => {
        return n.charAt(0).toUpperCase() + n.slice(1)
      }).join(' ')
    }else{
      return str.charAt(0).toUpperCase() + str.slice(1)
    }

  }
  return str;
}


/*
* 
* @param arr {Array}  
* @return 
* */
export function arrayUnique(arr) {
  var res = [];
  var json = {};
  for (var i = 0; i < arr.length; i++) {
    var key = arr[i]
    if (typeof arr[i] === 'object' && arr[i] != null) {
      key = JSON.stringify(arr[i])
    }
    if (!json[key] && arr[i]) {
      res.push(arr[i]);
      json[key] = 1;
    }
  }
  return res;
}

/**
 * 
 * @param arr 
 * @param item 
 * @return Boolean
 */
export function inArray(arr, item) {
  if (arr && arr.length && item!==undefined && arr.some((v) => v === item)) {
    return true;
  }
  else {
    return false
  }
}

/**
 * 
 * @param arr 
 * @param item 
 * @return Array undefault
 */
export function removeArrayItem(arr, item) {
  if (arr && arr.length) {
    var index = arr.indexOf(item);
    if (index > -1) {
      return arr.splice(index, 1);
    }
  }
}


//html
export function contenteditable(e) {

  e.preventDefault();
  let text = null;
  let textRange;
  if (window.clipboardData && clipboardData.setData) {
    // IE
    text = window.clipboardData.getData('text');
  }
  else {
    text = (e.originalEvent || e).clipboardData.getData('text/plain') || prompt('Enter the text here!');
  }
  if (document.body.createTextRange) {
    if (document.selection) {
      textRange = document.selection.createRange();
    }
    else if (window.getSelection) {
      let sel = window.getSelection();
      let range = sel.getRangeAt(0);

      // TextRange
      let tempEl = document.createElement("span");
      tempEl.innerHTML = "&#FEFF;";
      range.deleteContents();
      range.insertNode(tempEl);
      textRange = document.body.createTextRange();
      textRange.moveToElementText(tempEl);
      tempEl.parentNode.removeChild(tempEl);
    }
    textRange.text = text;
    textRange.collapse(false);
    textRange.select();
  }
  else {
    // Chrome
    document.execCommand("insertText", false, text);
  }
}

/*
* 
* @param obj 
* */
export function isObject(obj) {
  return Object.prototype.toString.call(obj) === '[object Object]'
}

/*
* 
* @param obj 
* */
export function isEmptyObject(obj) {
  //true
  if (isObject(obj)) {
    for (var i in obj) {
      return false;
    }
    return true;
  }
}
